body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  direction: rtl;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.title {
    align-items: center;
    display: flex;
    flex-direction: column;
}

.submit {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center !important;
    padding-left: 50px !important;
}
.serviceListClass{
width: 30%;
margin: 2%!important;
/* margin-right: 5%; */
}
.button-add-service{
    width: 15%;
    float: right;
    margin: 2%!important;
    margin-right:250px!important;
}

h1{
    margin: auto;
    text-align: center;

}
input{
    width: 15%;
}
.detalis{
    visibility:hidden
}
.update{
    width:40%;
    border:solid;
    border-color: rgb(141, 141, 143);
}
.margin{
    margin: 2%!important;
}
.tools{
    display: flex;
    justify-content: space-between;
    background-color: blueviolet;
    color: chartreuse;
}
:host{
    width: 100px;
    height: 70px;
}
